//FONT FAMILY
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400;1,700&display=swap');

*{

    margin:0;
    padding:0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}
body {
    display: flex;
    flex-direction: column;
    padding: 0.5em 1em;
}

.cart {
    display: flex;
    flex-direction: column;
    gap: 3em;
    padding: 2em;
    background-color: #F2F2F2;
    

    .product {
        display: flex;
        gap: 1em;

        .item-img { //image
            max-width: 8em;
            border-radius: 12px;
        }
    }
    
    .item-info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex-grow: 1; //thanks Kevin Powell

        .product-metadata {
            
            .name-price {
                display: flex;
                flex-direction: column;
                gap: 1em;

                h4 {
                    white-space: nowrap;
                }
                
                .price {
                    display: flex;
                    gap: 1em;
                }
            }
        }

        .quantity {
            display: flex;
            gap: 1em;
            border: 1px solid #828282;
            justify-content: center;
            padding: 10px;
            border-radius: 10px;
            max-width: 7em;

            i {
                color: #828282;
                background-color: #E0E0E0;
                border-radius: 5px;
                padding: 2px;
                cursor: pointer;
            }
        }
    }

    //PAYOUT
    .payout {
        display: flex;
        flex-direction: column;
        gap: 0.5em;

        hr {
            color: #BDBDBD;
        }

        .shipping, .total {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}

